import { AxiosError, AxiosResponse } from 'axios';
import {
  NearpayApiErrorResponse,
  NearpayApiSuccessResponse,
} from './interfaces';

function isAxiosError(error: any): error is AxiosError {
  return error.isAxiosError === true;
}

export async function wrapRequest<T>(
  request: () => Promise<AxiosResponse>,
): Promise<T> {
  try {
    const response = await request();

    const successResponse = response.data as NearpayApiSuccessResponse<T>;
    return successResponse.result;
  } catch (error) {
    if (isAxiosError(error) && error.response) {
      const errorResponse = error.response.data as NearpayApiErrorResponse;
      throw errorResponse.error;
    }

    throw error;
  }
}
