import "./donate-form.scss";

import { block } from "bem-cn";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { validateEverything } from "./lib";
import { NpLogoIcon, NpNextIcon } from "ui";
import { ProceedButton } from "components/proceed-button";
import { RatesRecord } from "lib/api/rates/interfaces";
import { ICurrency } from "lib/api/currencies";
import { CurrencyInput } from "components/donate-form/currency-input";
import classNames from "classnames";
import { WidgetContainer } from "ui/widget-container/widget-container";
import { useState } from "react";

const b = block("nearpay-ua-donate-form");
const bDescription = block("nearpay-ua-donate-form-description");

interface Props {
  rates: RatesRecord;
  currency: ICurrency;
  defaultAmount: number;
}
export const ChooseAmountForm: React.FC<Props> = ({
  rates,
  currency,
  defaultAmount,
}) => {
  const [amount, setAmount] = useState<number | undefined>(defaultAmount);
  const navigate = useNavigate();

  return (
    <WidgetContainer className={b()}>
      <div className={b("title")}>Donate For Ukraine</div>
      <div className={bDescription()}>
        <div>
          <div className={bDescription("label")}>Product type</div>
          <div>Donate</div>
        </div>
        <div>
          <div className={bDescription("label")}>Product name</div>
          <div>For Ukraine</div>
        </div>
      </div>

      <CurrencyInput
        label={"Choose amount"}
        value={amount?.toString()}
        currency={currency}
        onValueChange={(v) => setAmount(v.floatValue)}
        onCurrencySelectClick={() => {}}
        selectDisabled={true}
        className="mb-4"
      />
      <div className="flex-grow"></div>
      {
        <ProceedButton
          disabled={!amount}
          onClick={() => {
            navigate({
              pathname: "/donate",
              search: `?amount=${amount}&currency=${currency.ticker}`,
            });
          }}
          icon={<NpNextIcon />}
        >
          Go to donation
        </ProceedButton>
      }

      <a
        href="http://nearpay.co"
        target="_blank"
        rel="noopener noreferrer"
        className={classNames(b("link"), "w-full", "text-center")}
      >
        <span className={b("link_gray")}>Powered by</span>
        {"  "}
        <NpLogoIcon className={b("link_icon")} width="20" height="15" />
        {"  "}
        <span>NearPay</span>
      </a>
    </WidgetContainer>
  );
};
