import './layout.scss';
import { block } from 'bem-cn';
import classNames from 'classnames';
import React, { HTMLAttributes } from 'react';

const b = block('nearpay-ua-layout');
const bMain = block('nearpay-ua-main');
const bFooter = block('nearpay-ua-footer');

type Props = {
  children: React.ReactNode;
};
export const Layout: React.FC<Props> = (props) => {
  const { children } = props;

  return (
    <div className={b()}>
      <header className={b('header')}>
        <img
          className={b('logo')}
          src="/assets/img/logo.png"
          alt="nearpay-logo"
        />
      </header>
      {children}
    </div>
  );
};

export const Main: React.FC<Props> = (props) => {
  return <main className={bMain()}>{props.children}</main>;
};

export const Footer: React.FC<React.HTMLAttributes<any>> = ({
  className,
  ...rest
}: HTMLAttributes<HTMLDivElement>) => {
  return (
    <footer className={classNames(bFooter(), className)} {...rest}>
      <div className={bFooter('agreement')}>
        By continuing, you agree to this{' '}
        <a
          href="https://nearpay.co/privacy_policy"
          target="_blank"
          rel="noopener noreferrer"
        >
          privacy policy
        </a>{' '}
        and{' '}
        <a
          href="https://nearpay.co/terms_of_use"
          target="_blank"
          rel="noopener noreferrer"
        >
          terms of service
        </a>
      </div>
      <a className={bFooter('support')} href="mailto:support@nearpay.co">
        Support
      </a>
    </footer>
  );
};
