import './donate-form.scss';

import { block } from 'bem-cn';
import { useNear } from 'lib/near';
import { Link, Navigate } from 'react-router-dom';
import { convertToNear, useCallbackUrl, validateEverything } from './lib';
import { NpLogoIcon } from 'ui';
import { ProceedButton } from 'components/proceed-button';
import { RatesRecord } from 'lib/api/rates/interfaces';
import { ICurrency } from 'lib/api/currencies';
import { CurrencyInput } from 'components/donate-form/currency-input';
import classNames from 'classnames';
import { WidgetContainer } from 'ui/widget-container/widget-container';
import { utils } from 'near-api-js';

const b = block('nearpay-ua-donate-form');
const bDescription = block('nearpay-ua-donate-form-description');

interface Props {
  rates: RatesRecord;
  currency: ICurrency;
  amount: number;
}
export const DonateForm: React.FC<Props> = ({ rates, currency, amount }) => {
  const near = useNear();

  const callbackUrl = useCallbackUrl();

  const { hasAllParameters, isRateFound, isRateNotFound, isRatesLoaded } =
    validateEverything(amount, currency.ticker, rates);

  if (!hasAllParameters) {
    return <Navigate to="/" />;
  }

  const yoktoNearAmount = convertToNear(amount, currency.ticker, rates);
  let nearAmount = utils.format.formatNearAmount(yoktoNearAmount || '0', 2);

  return (
    <WidgetContainer className={b()}>
      <div className={b('title')}>Donate For Ukraine</div>
      <div className={bDescription()}>
        <div>
          <div className={bDescription('label')}>Product type</div>
          <div>Donate</div>
        </div>
        <div>
          <div className={bDescription('label')}>Product name</div>
          <div>For Ukraine</div>
        </div>
      </div>

      {isRateNotFound ? (
        <div>
          <p>Donations in {currency.viewedTicker} is not possible</p>
          <p>
            You may choose another currency <Link to="/">here</Link>
          </p>
        </div>
      ) : null}

      <CurrencyInput
        label={'Send to Ukraine '}
        value={amount.toString()}
        currency={currency}
        onValueChange={() => {}}
        onCurrencySelectClick={() => {}}
        selectDisabled={true}
        inputDisabled={true}
        className="mb-4"
      />
      <div className="flex-grow"></div>
      {!near.isReady ? null : !near.isAuthorized ? (
        <ProceedButton
          onClick={() =>
            near.login({
              successUrl: '',
              failureUrl: '',
            })
          }
          disabled={isRateNotFound}
        >
          Login via Near
        </ProceedButton>
      ) : (
        <ProceedButton
          onClick={() => {
            if (!yoktoNearAmount) return;

            near.send(yoktoNearAmount, callbackUrl.toString());
          }}
          disabled={isRateNotFound}
        >
          Make a donation (~{nearAmount} NEAR)
        </ProceedButton>
      )}

      <a
        href="http://nearpay.co"
        target="_blank"
        rel="noopener noreferrer"
        className={classNames(b('link'), 'w-full', 'text-center')}
      >
        <span className={b('link_gray')}>Powered by</span>
        {'  '}
        <NpLogoIcon className={b('link_icon')} width="20" height="15" />
        {'  '}
        <span>NearPay</span>
      </a>
    </WidgetContainer>
  );
};
