import { useSearchParams, useHref } from 'react-router-dom';

import { utils } from 'near-api-js';
import { RatesRecord } from 'lib/api/rates/interfaces';
const buildRateTicker = (currency: string) => currency + '_NEAR';

export function validateEverything(
  amount: number | null | undefined,
  currency: string | null | undefined,
  rates: RatesRecord | undefined,
) {
  const hasAllParameters =
    !!amount && !isNaN(amount) && amount > 0 && !!currency;
  const isRatesLoaded = !!rates;

  const rate = (rates || {})[buildRateTicker(currency || '')];
  const isRateNotFound = isRatesLoaded && !rate;
  const isRateFound = isRatesLoaded && !!rate;

  return {
    hasAllParameters,
    isRateFound,
    isRateNotFound,
    isRatesLoaded,
  };
}

export function convertToNear(
  amount: number | null,
  currency: string | null,
  rates: RatesRecord | undefined,
) {
  if (!amount || !currency || !rates) {
    return null;
  }

  const rate = rates[buildRateTicker(currency)];
  if (!rate) return null;

  const nearAmount = amount * rate.exchangeRate.multiplier;

  return utils.format.parseNearAmount(nearAmount.toString());
}

export function useCallbackUrl() {
  const [searchParams] = useSearchParams();
  const href = useHref('/callback');

  const amount = searchParams.get('amount');
  const currency = searchParams.get('currency');

  const callbackUrl = new URL(window.location.origin + href);
  callbackUrl.searchParams.set('currency', currency || '');
  callbackUrl.searchParams.set('amount', amount || '');

  return callbackUrl;
}
