import { useNearContext } from './near-context';
import { transactions, utils } from 'near-api-js';

type RedirectConfig = {
  successUrl: string;
  failureUrl: string;
};

const NEAR_NOT_READY = new Error('Near is not ready yet');
const fourBillUkraineAccount = process.env.REACT_APP_4BILL_NEAR_ACCOUNT;

export function useNear(): {
  isAuthorized: boolean;
  isReady: boolean;
  login: (redirects: RedirectConfig) => Promise<void>;
  // amount should be in yoktonears (if we want to send 1 NEAR, then 1e18 should be provided as parameter)
  send: (amount: string, callbackUrl: string) => Promise<void>;
} {
  const near = useNearContext();
  return {
    // whether we have access to username
    isAuthorized: near !== null && !!near.walletConnection.getAccountId(),
    // to manage loaders, because NEAR initialization is async operation
    isReady: near !== null,
    login: async (redirect: RedirectConfig) => {
      if (!near) throw NEAR_NOT_READY;

      await near.walletConnection.requestSignIn({
        successUrl: redirect.successUrl,
        failureUrl: redirect.failureUrl,
      });
    },
    send: async (amount: string, callbackUrl: string) => {
      if (!near) throw NEAR_NOT_READY;
      const accountId = near.walletConnection.getAccountId();
      const keypair = utils.KeyPair.fromRandom('ed25519');
      const publicKeyRandom = keypair.getPublicKey();
      const block = await near.provider.block({ finality: 'final' });
      const recentBlockHash = utils.serialize.base_decode(block.header.hash);

      const transaction = transactions.createTransaction(
        accountId,
        publicKeyRandom,
        fourBillUkraineAccount || '',
        1,
        [transactions.transfer(amount)],
        recentBlockHash,
      );

      await near.walletConnection.requestSignTransactions({
        transactions: [transaction],
        callbackUrl,
      });
    },
  };
}
