import { NearContextProvider } from 'lib/near/near-context';
import { useToastQuery } from 'lib/toast';
import { CallbackPage } from 'pages/callback-page/callback-page';
import { BrowserRouter, Routes, Route, Outlet } from 'react-router-dom';
import { ChooseAmountPage } from './pages/choose-amount-page';
import { DonatePage } from './pages/donate-page';
import { ThankYouPage } from './pages/thank-you-page';

function AppContainer() {
  useToastQuery();

  return (
    <div>
      <Outlet />
    </div>
  );
}
function App() {
  return (
    <NearContextProvider>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<AppContainer />}>
            {/* Page required `currency` & `amount` queries to be defined, handles both auth/not authed state */}
            <Route path="/donate" element={<DonatePage />}></Route>
            {/* Page to land user on after they made a donation */}
            <Route path="/thank-you" element={<ThankYouPage />}></Route>
            {/* Page to split redirects between succesfull/insuccesful after near-wallet outcome */}
            <Route path="/callback" element={<CallbackPage />}></Route>
            {/* Page for direct users, without any query parameters */}
            <Route path="/" element={<ChooseAmountPage />}></Route>
          </Route>
        </Routes>
      </BrowserRouter>
    </NearContextProvider>
  );
}

export default App;
