import { Loader } from "components/kit";
import { Footer, Layout, Main } from "ui";
import { WidgetContainer } from "ui/widget-container";
import useSWR from "swr";
import { currenciesApi, ratesApi } from "lib/api";
import { environment } from "lib/config";
import { RatesRecord } from "lib/api/rates/interfaces";
import { ChooseAmountForm } from "components/donate-form";

const currency = environment.REACT_APP_DEFAULT_CURRENCY;
const amount = parseInt(environment.REACT_APP_DEFAULT_AMOUNT, 10);

console.log(currency);

export function ChooseAmountPage() {
  const ratesSwr = useSWR("rates", ratesApi.fetchRates);
  const currenciesSwr = useSWR("currencies", currenciesApi.fetchCurrencies);

  const isLoaded = !!ratesSwr.data && !!currenciesSwr.data;

  let currencyObject = null;
  if (isLoaded && currenciesSwr.data) {
    currencyObject = currenciesSwr.data.find((c) => c.ticker === currency);
  }

  if (!isLoaded) {
    return (
      <Layout>
        <Main>
          <WidgetContainer>
            <div className="w-48 my-auto mx-auto">
              <Loader />
            </div>
          </WidgetContainer>
        </Main>
        <Footer className="mt-8" />
      </Layout>
    );
  }

  return (
    <Layout>
      <Main>
        {currencyObject ? (
          <ChooseAmountForm
            rates={ratesSwr.data as RatesRecord}
            currency={currencyObject}
            defaultAmount={amount}
          />
        ) : (
          <WidgetContainer>
            <div className="my-auto text-center">
              <h1 className="text-lg uppercase font-bold mb-6">
                Unknown currency
              </h1>
              <p>
                Convertation from{" "}
                <span className="font-semibold">{currency}</span> is not
                possible
              </p>
              <p>
                Find available options{" "}
                <a
                  href={environment.REACT_APP_4BILL_URL}
                  className="hover:underline font-bold"
                >
                  here
                </a>
              </p>
            </div>
          </WidgetContainer>
        )}
      </Main>
      <Footer className="mt-8" />
    </Layout>
  );
}
